import {
  ApolloClient,
  ApolloLink,
  fromPromise,
  HttpLink,
  InMemoryCache,
} from '@apollo/client';
import { setContext } from '@apollo/client/link/context';
import { onError } from '@apollo/client/link/error';
import axios, { getResponseError } from './axios';
// import { RestLink } from 'apollo-link-rest';

const httpLink = new HttpLink({
  uri: 'http://localhost:3001/api/graphql',
  credentials: 'include',
});

const authLink = setContext((_, { headers }) => {
  console.log('get token');
  const accessTokenPayload = localStorage.getItem('x-access-payload');
  return {
    headers: {
      ...headers,
      'x-access-payload': accessTokenPayload ? `${accessTokenPayload}` : '',
    },
  };
});

let isRefreshing = false;
let pendingRequests = [];

const resolvePendingRequests = () => {
  pendingRequests.map((callback) => callback());
  pendingRequests = [];
};

const errorLink = onError(
  ({ graphQLErrors, networkError, operation, forward }) => {
    if (graphQLErrors) {
      for (let err of graphQLErrors) {
        switch (err.extensions.code) {
          case 'UNAUTHENTICATED': {
            let forward$;

            if (!isRefreshing) {
              isRefreshing = true;
              forward$ = fromPromise(
                getNewToken()
                  .then(({ accessTokenPayload, refreshTokenPayload }) => {
                    // Store the new tokens for your auth link
                    localStorage.setItem(
                      'x-refresh-payload',
                      refreshTokenPayload
                    );
                    localStorage.setItem(
                      'x-access-payload',
                      accessTokenPayload
                    );
                    resolvePendingRequests();
                    return accessTokenPayload;
                  })
                  .catch((error) => {
                    pendingRequests = [];
                    return error;
                  })
                  .finally(() => {
                    isRefreshing = false;
                  })
              ).filter((value) => Boolean(value));
            } else {
              // Will only emit once the Promise is resolved
              forward$ = fromPromise(
                new Promise((resolve) => {
                  pendingRequests.push(() => resolve());
                })
              );
            }

            return forward$.flatMap((accessTokenPayload) => {
              const oldHeaders = operation.getContext().headers;
              operation.setContext({
                headers: {
                  ...oldHeaders,
                  'x-access-payload': accessTokenPayload,
                },
              });
              return forward(operation);
            });
          }
        }
      }
    }
    if (networkError) {
      console.log(`[Network error]: ${networkError}`);
      // if you would also like to retry automatically on
      // network errors, we recommend that you use
      // apollo-link-retry
    }
  }
);

async function getNewToken() {
  const refreshTokenPayload$ = localStorage.getItem('x-refresh-payload');
  const { accessTokenPayload, refreshTokenPayload } = await axios.get(
    `${process.env.REACT_APP_PUBLIC_BASE_API_URL}/api/auth/refresh`,
    {
      headers: {
        'Content-Type': 'application/json',
        'x-refresh-payload': refreshTokenPayload$,
      },
    }
  );

  return { accessTokenPayload, refreshTokenPayload };
}

export const client = new ApolloClient({
  cache: new InMemoryCache(),
  link: ApolloLink.from([authLink, errorLink, httpLink]),
});
