import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

import { setUser } from '@/modules/user/user.slice';
import axios from '@/services/axios';

const authSlice = createSlice({
  name: 'auth',
  initialState: {
    isAuthenticated: false,
  },
  reducers: {
    logoutSuccess: (state) => {
      state.isAuthenticated = false;
    },
    authSuccess: (state) => {
      state.isAuthenticated = true;
    },
  },
});

export const login = createAsyncThunk(
  'login',
  async ({ email, password }, { dispatch }) => {
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_PUBLIC_BASE_API_URL}/api/auth/login`,
        { email, password }
      );
      console.log('ðŸš€ ~ file: auth.slice.js ~ line 29 ~ data', data);

      localStorage.setItem('x-access-payload', data.accessTokenPayload);
      localStorage.setItem('x-refresh-payload', data.refreshTokenPayload);

      dispatch(
        setUser({ name: 'hieu', permissions: new Set(data.permissions) })
      );

      return { data };
    } catch (error) {
      return { error };
    }
  }
);

